import React, { useState } from 'react';
import '../styles/Chatbot.css';

const Chatbot = ({ onSave }) => {
  const [messages, setMessages] = useState([]);
  const [inputMessage, setInputMessage] = useState('');
  const [extractedInfo, setExtractedInfo] = useState(null);

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!inputMessage.trim()) return;

    //   
    const newMessages = [...messages, { text: inputMessage, type: 'user' }];
    setMessages(newMessages);

    //    
    const extracted = extractAssignmentInfo(inputMessage);
    if (extracted.title !== '  ' || extracted.deadline !== '  ') {
      setExtractedInfo(extracted);
    }

    //   
    setTimeout(() => {
      setMessages([...newMessages, {
        text: '  .    .',
        type: 'bot'
      }]);
    }, 500);

    setInputMessage('');
  };

  const handleSaveToCalendar = () => {
    if (!extractedInfo) return;

    const deadlineDate = parseDeadlineDate(extractedInfo.deadline);
    if (!deadlineDate) {
      alert('   .');
      return;
    }

    const eventData = {
      title: extractedInfo.title,
      type: 'assignment',
      date: deadlineDate,
      time: '23:59',
      endTime: '23:59',
      description: `: ${extractedInfo.points}\n: ${extractedInfo.location}`,
      priority: 'high',
      color: '#FF5733' //   
    };

    onSave(eventData);
    setMessages([...messages, {
      text: '  .',
      type: 'bot'
    }]);
    setExtractedInfo(null);
  };

  const handleEditAndSave = () => {
    if (!extractedInfo) return;

    const deadlineDate = parseDeadlineDate(extractedInfo.deadline);
    if (!deadlineDate) {
      alert('   .');
      return;
    }

    const eventData = {
      title: extractedInfo.title,
      type: 'assignment',
      date: deadlineDate,
      time: '23:59',
      endTime: '23:59',
      description: `: ${extractedInfo.points}\n: ${extractedInfo.location}`,
      priority: 'high',
      color: '#FF5733' //   
    };

    onSave(eventData);
    setMessages([...messages, {
      text: '  .',
      type: 'bot'
    }]);
    setExtractedInfo(null);
  };

  const parseDeadlineDate = (deadlineStr) => {
    if (!deadlineStr || deadlineStr === '  ') return null;

    //    
    const patterns = [
      {
        regex: /(\d{4})[-\/]\s*(\d{1,2})[-\/]\s*(\d{1,2})?/,
        handler: (matches) => new Date(matches[1], matches[2] - 1, matches[3])
      },
      {
        regex: /(\d{1,2})\/(\d{1,2})\/(\d{4})/,
        handler: (matches) => new Date(matches[3], matches[1] - 1, matches[2])
      },
      {
        regex: /(\d{1,2})\s*(\d{1,2})/,
        handler: (matches) => {
          const now = new Date();
          return new Date(now.getFullYear(), matches[1] - 1, matches[2]);
        }
      }
    ];

    for (const { regex, handler } of patterns) {
      const matches = deadlineStr.match(regex);
      if (matches) {
        return handler(matches);
      }
    }

    return null;
  };

  const extractAssignmentInfo = (text) => {
    const extracted = {
      title: '  ',
      deadline: '  ',
      points: '  ',
      location: '  '
    };

    //  
    const titlePatterns = [
      /[\s]*[:]\s*(.+?)(?=\n||||)/i,
      /[\s]*[:]\s*(.+?)(?=\n||||)/i,
      /[\[<](.+?)[\]>]/,
      /^(.+?)(?=|assignment|homework)/i
    ];

    for (const pattern of titlePatterns) {
      const match = text.match(pattern);
      if (match && match[1]) {
        extracted.title = match[1].trim();
        break;
      }
    }

    //  
    const deadlinePatterns = [
      /?[\s]*[:]\s*(\d{4}[-\/]\s*\d{1,2}[-\/]\s*\d{1,2}?)/i,
      /?[\s]*[:]\s*(\d{4}[-\/]\s*\d{1,2}[-\/]\s*\d{1,2}?)/i,
      /[\s]*[:]\s*(\d{4}[-\/]\s*\d{1,2}[-\/]\s*\d{1,2}?)/i,
      /(\d{4}[-\/]\s*\d{1,2}[-\/]\s*\d{1,2}?)/i,
      /(\d{1,2}\/\d{1,2}\/?\d{0,4})/,
      /(\d{1,2}\s*\d{1,2})/
    ];

    for (const pattern of deadlinePatterns) {
      const match = text.match(pattern);
      if (match && match[1]) {
        extracted.deadline = match[1].trim();
        break;
      }
    }

    //  
    const pointsPatterns = [
      /[\s]*[:]\s*(\d+)\s*/i,
      /[\s]*[:]\s*(\d+)\s*/i,
      /(\d+)\s*\s*/i,
      /(\d+)\s*/i
    ];

    for (const pattern of pointsPatterns) {
      const match = text.match(pattern);
      if (match && match[1]) {
        extracted.points = match[1] + '';
        break;
      }
    }

    //   
    const locationPatterns = [
      /[\s]*[:]\s*(.+?)(?=\n|$)/i,
      /[\s]*[:]\s*(.+?)(?=\n|$)/i,
      /[\s]*[:]\s*(.+?)(?=\n|$)/i
    ];

    for (const pattern of locationPatterns) {
      const match = text.match(pattern);
      if (match && match[1]) {
        extracted.location = match[1].trim();
        break;
      }
    }

    return extracted;
  };

  return (
    <div className="chatbot">
      <div className="chat-messages">
        {messages.map((message, index) => (
          <div key={index} className={`message ${message.type}`}>
            {message.text}
          </div>
        ))}
      </div>

      {extractedInfo && (
        <div className="extracted-info">
          <h4> </h4>
          <div className="info-grid">
            <div className="info-item">
              <label>:</label>
              <span>{extractedInfo.title}</span>
            </div>
            <div className="info-item">
              <label>:</label>
              <span>{extractedInfo.deadline}</span>
            </div>
            <div className="info-item">
              <label>:</label>
              <span>{extractedInfo.points}</span>
            </div>
            <div className="info-item">
              <label>:</label>
              <span>{extractedInfo.location}</span>
            </div>
          </div>
          <div className="extracted-actions">
            <button 
              className="save-extracted-button"
              onClick={handleSaveToCalendar}
            >
               
            </button>
            <button 
              className="edit-extracted-button"
              onClick={handleEditAndSave}
            >
                
            </button>
          </div>
        </div>
      )}

      <form className="chat-input" onSubmit={handleSubmit}>
        <input
          type="text"
          value={inputMessage}
          onChange={(e) => setInputMessage(e.target.value)}
          placeholder="  ..."
        />
        <button type="submit"></button>
      </form>
    </div>
  );
};

export default Chatbot; 