import React, { useState, useEffect } from 'react';
import Calendar from './components/Calendar';
import EventList from './components/EventList';
import EventForm from './components/EventForm';
import Chatbot from './components/Chatbot';
import { format, isSameDay, isToday, addDays } from 'date-fns';
import './App.css';

function App() {
  const [events, setEvents] = useState([]);
  const [selectedDate, setSelectedDate] = useState(new Date());
  const [showEventForm, setShowEventForm] = useState(false);
  const [editingEvent, setEditingEvent] = useState(null);
  const [activeTab, setActiveTab] = useState('events'); // 'events'  'chatbot'

  //    
  useEffect(() => {
    const savedEvents = localStorage.getItem('events');
    if (savedEvents) {
      try {
        const parsedEvents = JSON.parse(savedEvents);
        //   Date  
        const eventsWithDates = parsedEvents.map(event => ({
          ...event,
          date: new Date(event.date)
        }));
        setEvents(eventsWithDates);
      } catch (error) {
        console.error('Failed to parse saved events:', error);
      }
    }
  }, []);

  //      
  useEffect(() => {
    try {
      localStorage.setItem('events', JSON.stringify(events));
    } catch (error) {
      console.error('Failed to save events:', error);
    }
  }, [events]);

  //   
  useEffect(() => {
    const checkDeadlines = () => {
      const today = new Date();
      const tomorrow = addDays(today, 1);

      events.forEach(event => {
        const eventDate = new Date(event.date);
        
        //   
        if (isToday(eventDate)) {
          alert(`[ ]  "${event.title}" ${getEventTypeName(event.type)} !`);
        }
        //   
        else if (isSameDay(eventDate, tomorrow)) {
          alert(`[ ]  "${event.title}" ${getEventTypeName(event.type)} !`);
        }
      });
    };

    //      
    checkDeadlines();

    //   
    const now = new Date();
    const tomorrow = new Date(now.getFullYear(), now.getMonth(), now.getDate() + 1);
    const timeToMidnight = tomorrow.getTime() - now.getTime();

    const midnightTimeout = setTimeout(() => {
      checkDeadlines();
      //  24 
      const dailyInterval = setInterval(checkDeadlines, 24 * 60 * 60 * 1000);
      return () => clearInterval(dailyInterval);
    }, timeToMidnight);

    return () => clearTimeout(midnightTimeout);
  }, [events]);

  const getEventTypeName = (type) => {
    switch (type) {
      case 'assignment':
        return '';
      case 'exam':
        return '';
      case 'lecture':
        return '';
      case 'meeting':
        return '';
      case 'academic':
        return '';
      case 'personal':
        return '';
      default:
        return '';
    }
  };

  const addEvent = (eventData) => {
    const newEvent = {
      ...eventData,
      id: eventData.id || Date.now().toString(),
      date: new Date(eventData.date)
    };

    if (editingEvent) {
      setEvents(events.map(event => 
        event.id === editingEvent.id ? newEvent : event
      ));
    } else {
      setEvents([...events, newEvent]);
    }
    setShowEventForm(false);
    setEditingEvent(null);
  };

  const deleteEvent = (eventId) => {
    if (window.confirm('  ?')) {
      setEvents(events.filter(event => event.id !== eventId));
    }
  };

  const editEvent = (event) => {
    setEditingEvent(event);
    setShowEventForm(true);
  };

  return (
    <div className="app">
      <header className="app-header">
        <h1>    </h1>
      </header>
      <main className="app-main">
        <Calendar 
          events={events} 
          selectedDate={selectedDate}
          setSelectedDate={setSelectedDate}
          onAddEvent={() => {
            setEditingEvent(null);
            setShowEventForm(true);
          }}
        />
        <div className="sidebar">
          <div className="tab-navigation">
            <button 
              className={`tab-button ${activeTab === 'events' ? 'active' : ''}`}
              onClick={() => setActiveTab('events')}
            >
               
            </button>
            <button 
              className={`tab-button ${activeTab === 'chatbot' ? 'active' : ''}`}
              onClick={() => setActiveTab('chatbot')}
            >
              
            </button>
          </div>

          {activeTab === 'events' ? (
            <>
              <EventList 
                events={events.filter(event => {
                  const eventDate = new Date(event.date);
                  return isSameDay(eventDate, selectedDate);
                })}
                onDelete={deleteEvent}
                onEdit={editEvent}
              />
              {showEventForm && (
                <EventForm 
                  selectedDate={selectedDate}
                  onSave={addEvent}
                  onCancel={() => {
                    setShowEventForm(false);
                    setEditingEvent(null);
                  }}
                  event={editingEvent}
                />
              )}
              {!showEventForm && (
                <button 
                  className="add-event-button"
                  onClick={() => {
                    setEditingEvent(null);
                    setShowEventForm(true);
                  }}
                >
                   
                </button>
              )}
            </>
          ) : (
            <Chatbot onSave={addEvent} />
          )}
        </div>
      </main>
    </div>
  );
}

export default App;