import React, { useState, useEffect } from 'react';
import { format } from 'date-fns';
import '../styles/EventForm.css';

const EVENT_TYPES = [
  { id: 'assignment', label: '', defaultColor: '#FF5733' },
  { id: 'exam', label: '', defaultColor: '#C70039' },
  { id: 'lecture', label: '', defaultColor: '#900C3F' },
  { id: 'meeting', label: '', defaultColor: '#581845' },
  { id: 'academic', label: '', defaultColor: '#2874A6' },
  { id: 'personal', label: '', defaultColor: '#229954' }
];

const EventForm = ({ selectedDate, onSave, onCancel, event }) => {
  const [title, setTitle] = useState('');
  const [description, setDescription] = useState('');
  const [date, setDate] = useState(format(selectedDate, 'yyyy-MM-dd'));
  const [time, setTime] = useState('09:00');
  const [endTime, setEndTime] = useState('10:00');
  const [type, setType] = useState('assignment');
  const [priority, setPriority] = useState('medium');
  const [color, setColor] = useState(EVENT_TYPES[0].defaultColor);
  const [tag, setTag] = useState('');

  useEffect(() => {
    if (event) {
      setTitle(event.title);
      setDescription(event.description || '');
      setDate(format(new Date(event.date), 'yyyy-MM-dd'));
      setTime(event.time || '09:00');
      setEndTime(event.endTime || '10:00');
      setType(event.type || 'assignment');
      setPriority(event.priority || 'medium');
      setColor(event.color || EVENT_TYPES.find(t => t.id === event.type)?.defaultColor || EVENT_TYPES[0].defaultColor);
      setTag(event.tag || '');
    } else {
      setDate(format(selectedDate, 'yyyy-MM-dd'));
      setColor(EVENT_TYPES[0].defaultColor);
    }
  }, [event, selectedDate]);

  useEffect(() => {
    //       
    const selectedType = EVENT_TYPES.find(t => t.id === type);
    if (selectedType) {
      setColor(selectedType.defaultColor);
    }
  }, [type]);

  const handleSubmit = (e) => {
    e.preventDefault();
    const eventData = {
      id: event ? event.id : Date.now().toString(),
      title,
      description,
      date: new Date(`${date}T${time}`),
      time,
      endTime,
      type,
      priority,
      color,
      tag
    };
    onSave(eventData);
  };

  return (
    <form className="event-form" onSubmit={handleSubmit}>
      <h3>{event ? ' ' : '  '}</h3>
      
      <div className="form-group">
        <label htmlFor="title"></label>
        <input
          id="title"
          type="text"
          value={title}
          onChange={(e) => setTitle(e.target.value)}
          required
        />
      </div>
      
      <div className="form-group">
        <label htmlFor="type"></label>
        <select
          id="type"
          value={type}
          onChange={(e) => setType(e.target.value)}
        >
          {EVENT_TYPES.map(type => (
            <option key={type.id} value={type.id}>
              {type.label}
            </option>
          ))}
        </select>
      </div>
      
      <div className="form-row">
        <div className="form-group">
          <label htmlFor="date"></label>
          <input
            id="date"
            type="date"
            value={date}
            onChange={(e) => setDate(e.target.value)}
            required
          />
        </div>
        
        <div className="form-group">
          <label htmlFor="time"> </label>
          <input
            id="time"
            type="time"
            value={time}
            onChange={(e) => setTime(e.target.value)}
            required
          />
        </div>
        
        <div className="form-group">
          <label htmlFor="endTime"> </label>
          <input
            id="endTime"
            type="time"
            value={endTime}
            onChange={(e) => setEndTime(e.target.value)}
            required
          />
        </div>
      </div>
      
      <div className="form-group">
        <label htmlFor="priority"></label>
        <select
          id="priority"
          value={priority}
          onChange={(e) => setPriority(e.target.value)}
        >
          <option value="high"></option>
          <option value="medium"></option>
          <option value="low"></option>
        </select>
      </div>

      <div className="form-row">
        <div className="form-group">
          <label htmlFor="color"></label>
          <input
            id="color"
            type="color"
            value={color}
            onChange={(e) => setColor(e.target.value)}
          />
        </div>
        
        <div className="form-group">
          <label htmlFor="tag"></label>
          <input
            id="tag"
            type="text"
            value={tag}
            onChange={(e) => setTag(e.target.value)}
            placeholder="  ()"
          />
        </div>
      </div>
      
      <div className="form-group">
        <label htmlFor="description"></label>
        <textarea
          id="description"
          value={description}
          onChange={(e) => setDescription(e.target.value)}
          rows="3"
        />
      </div>
      
      <div className="form-buttons">
        <button type="button" className="cancel-button" onClick={onCancel}>
          
        </button>
        <button type="submit" className="save-button">
          
        </button>
      </div>
    </form>
  );
};

export default EventForm;